.home {
  text-align: center;
  &__start_page {
    padding-bottom: 10vh;
    background: no-repeat;
    background-image: url(../../assets/images/background-image-home.png);
    background-size: cover;
    max-height: 85vh;
    position: relative;
    top: 15vh;
    z-index: 0;
  }

  &__logo {
    margin-top: 30vh;
    margin-bottom: 0vh;
    width: 25vh;
    height: auto;
  }

  &__title {
    margin-top: 5vh;
    margin-bottom: 0.9em;
    width: 100vw;
    height: var(--h1);
    font-style: normal;
    font-weight: 600;
    font-size: var(--h1);
    line-height: 7.8rem;
    letter-spacing: 0.025rem;
    color: var(--blue);
    text-align: center;
  }

  &__description {
    margin-bottom: 20vh;
    width: 100vw;
    font-style: normal;
    font-weight: 600;
    font-size: var(--h2);
    line-height: 3em;
    align-items: center;
    text-transform: capitalize;
    color: var(--white);
    text-align: center;
  }
}
